import React from "react";
import { Navbar, Nav } from "react-bootstrap";
import "./style.css";


class NavBar extends React.Component {

    render () {
        return (

    <Navbar bg="dark" expand="lg">
        <Navbar.Brand href="#home" className="name-logo">jonDatz</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" className=" navbar-dark" />
        <Navbar.Collapse id="basic-navbar-nav links">
            <Nav className="ml-auto">
            <Nav.Link href="#home" className="link-text" >Home</Nav.Link>
            <Nav.Link disabled href="#link" className="link-text" >Blog</Nav.Link>
            </Nav>
        </Navbar.Collapse>
        </Navbar>            
        )
    }
}

export default NavBar;