import React from "react";
import { Jumbotron, Button } from "react-bootstrap";
import RoundIMG from "../RoundIMG/RoundIMG";
import "./style.css";

class Header extends React.Component {
  render() {
    return (
      <Jumbotron fluid>
        <div className=" container">
          <div className="row">

            <div className="col-md-3">
                <RoundIMG />
            </div>
            <div className="col-md-9">
            {/* <h1 className="head-text" id="full-stack"><span> FULL STACK </span></h1> */}
            <div className="head-space"> </div>
            <h1 className="head-text" id="web-dev"><span> WEB DEVELOPER </span></h1>
            <p className="header-p">
              Web developer with an eye for solving problems. I strive for clean, elegant design 
              <br /> while maintaining a strong focus on functionality.   
            </p>

            </div>
          </div>
        </div>
      </Jumbotron>
    );
  }
}

export default Header;
